<template>
  <div class="page">
    <button class="btn-blue" @click="start">{{isStart?'重新开始':'开始'}}</button>
    <div class="chessboard" @click="playChess" @mousemove="setCp" @mouseenter="showCp" ref="chessboard">
      <div class="center-point"></div>
      <div class="row" v-for="i in 13" :key="'r'+i" :style="{top: i*50-2+'px'}"></div>
      <div class="col" v-for="i in 13" :key="'c'+i" :style="{left: i*50-2+'px'}"></div>
      <div class="chess" v-for="(item,index) in list" :key="'_'+index" :class="item.isPlay" :style="{left: item.x+'px',top: item.y+'px'}"></div>
      <div class="chess-pointer" :style="{display: cpShow,left: cpLeft+'px',top: cpTop+'px'}">
        <span class="cp cp-l cp-t"></span>
        <span class="cp cp-l cp-b"></span>
        <span class="cp cp-r cp-t"></span>
        <span class="cp cp-r cp-b"></span>
      </div>
    </div>
  </div>
</template>

<script>
  var socket
  export default {
    name: 'HelloWorld',
    data() {
      return {
        list: [],
        keys: {},
        count: 0,
        val: '',
        left: 0,
        top: 0,
        cpShow: 'none',
        cpLeft: 0,
        cpTop: 0,
        isStart: false
      }
    },
    mounted() {
      let chessboard = this.$refs.chessboard;
      this.left = chessboard.offsetLeft-chessboard.offsetWidth/2;
      this.top = chessboard.offsetTop-chessboard.offsetHeight/2;
      this.setKeys();
      // socket = io.connect('http://localhost:3000');
      // socket.on('chat', res => {
      //   this.list.push(res);
      // });

    },
    created(){

    },
    methods: {
      start(){
        this.isStart = true;
        this.setKeys();
      },
      playChess(e) {
        if (this.isStart){
          let x = e.x-this.left;
          let y = e.y-this.top;
          let cx = (parseInt(x/50)+(x%50>30?1:0))*50;
          let cy = (parseInt(y/50)+(y%50>30?1:0))*50;
          let key = 'x'+cx+'_'+'y'+cy;
          let index = this.keys[key];
          if (!this.list[index].isPlay){
            this.list[index].isPlay = this.count%2>0?'white':'black';
            this.count += 1;
            this.list[index].x = cx;
            this.list[index].y = cy;
            setTimeout(()=>{
              this.testWinner(cx,cy);
            },100);
          }
        }
      },
      showCp(e){
        let x = e.x-this.left;
        let y = e.y-this.top;
        let cx = (parseInt(x/50)+(x%50>30?1:0))*50;
        let cy = (parseInt(y/50)+(y%50>30?1:0))*50;
        this.cpShow = 'block';
        this.cpLeft = cx;
        this.cpTop = cy;
      },
      setCp(e){
        let x = e.x-this.left;
        let y = e.y-this.top;
        let cx = (parseInt(x/50)+(x%50>30?1:0))*50;
        let cy = (parseInt(y/50)+(y%50>30?1:0))*50;
        if (cx!==this.cpLeft||cy!==this.cpTop){
          this.cpLeft = cx;
          this.cpTop = cy;
        }
      },
      setKeys () {
        let keys = {},index=0,list=[];
        for (let i=0;i<14;i++){
          for(let j=0;j<14;j++){
            let key = 'x'+i*50+'_y'+j*50;
            list.push({
              x: 0,
              y: 0,
              isPlay: false
            });
            keys[key] = index;
            index++;
          }
        }
        this.list = list;
        this.keys = keys;
      },
      testWinner(x,y){
        let count = [1,1,1,1];
        let key = 'x'+x+'_'+'y'+y;
        let index = this.keys[key];
        let curChess = this.list[index].isPlay;
        let pre, pos = [{h:[-1,0],f:[1,0]},{h:[-1,-1],f:[1,1]},{h:[0,-1],f:[0,1]},{h:[1,-1],f:[-1,1]}];
        for(let i=1;i<5;i++){
          pos.map((item,ind) => {
            if (item){
              if (item.h){
                key = 'x'+(x+50*item.h[0]*i)+'_'+'y'+(y+50*item.h[1]*i);
                index = this.keys[key];
                if (index!==undefined){
                  pre = this.list[index];
                  if (pre.isPlay===curChess){
                    count[ind]++;
                  }else {
                    pos[ind].h = false;
                  }
                }else {
                  pos[ind].h = false;
                }
              }
              if (item.f){
                key = 'x'+(x+50*item.f[0]*i)+'_'+'y'+(y+50*item.f[1]*i);
                index = this.keys[key];
                if (index!==undefined){
                  pre = this.list[index];
                  if (pre.isPlay===curChess){
                    count[ind]++;
                  }else {
                    pos[ind].f = false;
                  }
                }else {
                  pos[ind].f = false;
                }
              }
            }
          });
        }
        if (count[0]===5||count[2]===5||count[3]===5||count[1]===5){
          this.isStart = false;
          alert(curChess+' win~!');
        }else {
          console.log('no win')
        }
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  h1, h2 {
    font-weight: normal;
  }

  ul {
    list-style-type: none;
    padding: 0;
  }

  li {
    display: inline-block;
    margin: 0 10px;
  }

  a {
    color: #42b983;
  }
</style>



